@import "styles/colors";

.App {
  font-family: 'Roboto', sans-serif;
  background-color: $mainColor;
  color: $textColor;

  a {
    text-decoration: none;
    color: $textColor;
  }
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 80px;
  height: 50px;

  .logo {
    //width: 50px;
    //height: 50px;
    //background-color: red;

    img {
      height: 60px;
    }
  }

  .nav-links {
    ul {
      list-style-type: none;
      display: flex;

      li {
        padding: 0 10px;
      }
    }
  }

  .tel {
    font-size: 20px;

    span {
      color: $red;
    }
  }
}

.container {
  padding: 0 80px;
  height: calc(100vh - 70px);

  .main-page {
    display: flex;
    height: calc(100% - 170px);

    .img {
      display: flex;
      align-items: center;
    }

    .title {
      flex: 1;
      display: flex;
      justify-content: center;
      flex-direction: column;

      .title-text {
        font-size: 80px;
        font-weight: bold;
        margin-bottom: 30px;
        text-transform: uppercase;
      }

      .description {
        display: flex;
        align-items: center;
        font-size: 24px;
        text-transform: uppercase;
        font-weight: 400;

        span {
          display: block;
          width: 10px;
          height: 10px;
          background-color: $red;
          border-radius: 50%;
          margin: 0 10px;
        }
      }

      .description-text {
        font-size: 14px;
        margin: 20px 0 50px;
        line-height: 24px;
      }
    }

    .call-btn {
      width: fit-content;
      background-color: $red;
      border: none;
      padding: 10px 40px;
      border-radius: 2px;
      font-size: 16px;
      font-weight: 400;
      text-transform: uppercase;
    }
  }

  .main-page-footer {
    display: flex;
    justify-content: center;

    .main-page-footer-link {
      display: flex;
      flex-direction: column;
      align-items: center;
      flex: 1;
      text-transform: uppercase;

      .img {
        width: 100%;
        border-right: 1px solid #404040;
        border-top: 1px solid #404040;
        display: flex;
        justify-content: center;

        img {
          height: 100px;
          padding: 10px 0;
        }
      }

      h4 {
        margin: 10px 40px;
      }

      &:last-child {
        .img {
          border-right: none;
        }
      }
      &:after {

      }
    }
  }

  .why-choose-us {
    .row {
      margin: 300px 0;
      text-transform: uppercase;

      .title {
        margin-bottom: 30px;

        h1 {
          margin: 5px 0;
          font-size: 40px;
          font-weight: 500;

          span {
            color: $red;
            margin-left: 10px;
          }
        }

        &-sub {
          color: $red;
        }
      }

      .qualities {
        display: flex;
        padding: 40px 0 60px;
        border-top: 1px solid $border;
        border-bottom: 1px solid $border;

        &-item {
          display: flex;
          align-items: center;
          justify-content: center;
          flex-direction: column;
          flex: 1;
          color: $red;
          font-size: 120px;
          font-weight: 500;
          position: relative;

          span {
            color: #ffffff;
            display: block;
            font-size: 18px;
          }

          &:before {
            content: "";
            position: absolute;
            top: -40px;
            left: 50%;
            width: 1px;
            height: 35px;
            background-color: $border;
          }

          &:after {
            content: "";
            position: absolute;
            bottom: -60px;
            left: 50%;
            width: 1px;
            height: 35px;
            background-color: $border;
          }
        }
      }
    }

    .row-2 {
      margin: 300px 0;
      text-transform: uppercase;
      display: flex;

      &-block {
        flex: 1;
        text-align: right;
        display: flex;
        flex-direction: column;
        justify-content: center;

        h1 {
          margin: 5px 0;
          font-size: 40px;
          font-weight: 500;

          span {
            color: $red;
            margin-left: 10px;
          }
        }

        .title-sub {
          margin-top: 26px;
          font-size: 26px;
          font-weight: 500;
        }

        .call-me {
          flex: 1;
          text-align: left;
          margin-left: 30px;
          display: flex;
          align-items: center;

          img {
            height: 100px;
            width: 80px;
            margin-right: 20px;
          }

          a {
            font-size: 40px;
          }
        }
      }
    }
  }
}